<template>
  <div>
    <!-- 使用子组件 -->
    <!-- 第一种： 父向子传值 -->
    <!-- <z-header :title="'登陆'"> </z-header> -->
    <!-- 第二种： 插槽 -->
    <z-header>登陆</z-header>

    <!-- 登录区域 -->
    <section class="page">
      <div class="login-page">
        <!-- 登录框 -->
        <div class="login-wrap">
          <div class="login-text">
            <input
              @mouseenter="focusText"
              @mouseleave="blurText"
              v-model="username"
              type="text"
              class="login-username"
              placeholder="用户名/邮箱/已验证手机"
            />
            <i
              class="iconfont icon-close"
              data-close="username"
              v-show="!usernameClose"
              @click="clearText"
            ></i>
          </div>
          <div class="login-text">
            <input
              @mouseenter="focusText"
              @mouseleave="blurText"
              ref="passwordText"
              v-model="password"
              type="password"
              class="login-password"
              placeholder="请输入密码"
            />
            <div>
              <i
                class="iconfont icon-close"
                data-close="password"
                v-show="!passwordClose"
                @click="clearText"
              ></i>
              <i class="iconfont icon-eye1 eye" @click="changeType" v-if="!passwordType"></i>
              <i class="iconfont icon-eye eye" @click="changeType" v-else></i>
              <router-link tag="span" to="./forget-password">忘记密码</router-link>
            </div>
          </div>
        </div>
        <!-- 错误提示 -->
        <div class="login-error">{{errMsg}}</div>
        <button
          class="login-button"
          :class="{'active' : username.trim()&&password.trim()}"
          @click="getUserLogin"
        >登 录</button>
        <!-- 快捷导航 -->
        <div class="quick-nav">
          <router-link tag="span" class="register-button" to="./register">快速注册</router-link>
        </div>
        <!-- 其他登录方式 -->
        <div class="other-login">
          <div class="other-head">
            <i></i>
            <span>其它登录方式</span>
            <i></i>
          </div>
          <div class="other-con">
            <div class="login-icon">
              <i class="iconfont icon-tubiao215"></i>
              <span>QQ</span>
            </div>
            <div class="login-icon">
              <i class="iconfont icon-weixin1"></i>
              <span>微信</span>
            </div>
          </div>
          <p>
            登录即代表您已同意
            <a>知心购隐私政策</a>
          </p>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
// 导入自定义组件 z-header
import zHeader from "@/components/common/z-header";
import { catchUserLogin, catchUserInfo, catchCartList } from "@/api";

export default {
  // 注册子组件
  components: {
    zHeader
  },
  data() {
    return {
      username: "",
      password: "",
      errMsg: "",
      usernameClose: true,
      passwordClose: true,
      passwordType: 0
    };
  },
  methods: {
      // 获取购物车商品数据
    // getCartList() {
    //   catchCartList().then(res => {
    //     // console.log(res);
    //     // this.cartList = res;
    //     // 给每类 商品添加一个是否选中的属性
    //     let carts = res.filter(item => {
    //       item.checked = false;
    //       return item;
    //     });
    //     // console.log(carts);

    //     /**
    //      * $nextTick 等待 网路数据 请求完成后 再去更新 数据重现渲染 界面
    //      * */ 
    //     // DOM 还没有更新
    //     this.$nextTick(function() {
    //       // DOM 更新了
    //       this.$store.commit("saveShopcarts", carts);
    //     });
    //   });
    // },
    //   用户登陆
    getUserLogin() {
      // 封装参数
      let parame = {
        username: this.username,
        password: this.password
      };
      catchUserLogin(parame).then(res => {
        // console.log(res);
        /*
      状态码 为-1 表示 用户名或密码错误
      状态码 为 0 表示成功
      状态码 为-2 表示 Token 失效
      */

        if (res.status == 0) {
          // 将 登陆 后的 位移凭证 保存
          localStorage.setItem("zxgToken", res.data.token);

          //将 获取 用户 信息接口 放置在 登陆完成之后， 一登录完成 就可以获取用户信息了
          catchUserInfo().then(res => {
            // 将 用户信息保存到 Vuex 中
            this.$store.commit("saveUserInfo", res);
            // 保存登陆状态到 localStorage
            localStorage.setItem("isLogin", true);
            // 登陆完成 跳转到 个人中心 页面
            // replace 取代当前页面
            this.$router.replace("/user");
          });
          
          // 获取购物车商品数据
          // this.getCartList()
          this.$store.dispatch('getCartList')

        } else {
          this.errMsg = res.msg
          this.$toast(res.msg)
        }
      });
    },

    focusText(e) {
      let $className = e.currentTarget.className;
      $className === "login-username"
        ? (this.usernameClose = false)
        : (this.passwordClose = false);
    },
    blurText(e) {
      let $className = e.currentTarget.className;
      $className === "login-username"
        ? (this.usernameClose = true)
        : (this.passwordClose = true);
    },
    clearText(e) {
      let $close = e.currentTarget.getAttribute("data-close");
      this[$close] = "";
    },
    changeType() {
      let $type = this.$refs.passwordText.getAttribute("type"),
        value = "";
      $type === "password" ? (value = "text") : (value = "password");
      this.$refs.passwordText.setAttribute("type", value);
      this.passwordType = !this.passwordType;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "@/common/style/mixin.scss";
.page {

  padding: 88px 50px 0;
  width: 100%;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;

  .login-page {
    margin-top: 60px;

    .login-wrap {
      width: 100%;

      .login-text {
        @include fj;
        width: 100%;
        height: 60px;
        line-height: 60px;
        padding: 20px 0;
        margin-top: 20px;
        border-bottom: 1px solid #dcdcdc;

        .iconfont {
          font-size: 26px;
          color: #cccccc;

          &.eye {
            padding: 0 30px;
            font-size: 40px;
            border-right: 1px solid #dcdcdc;
          }
        }

        input {
          width: 100%;
          height: 100%;
          margin-right: 20px;
          line-height: 60px;
          color: #222;
          font-size: 32px;

          &.login-password {
            width: 50%;
          }
        }

        span {
          padding-left: 20px;
          font-size: 30px;
        }

        div {
          display: flex;
        }
      }
    }

    .login-error {
      width: 100%;
      height: 40px;
      line-height: 40px;
      padding: 30px 0;
      color: $red;
      font-size: 26px;
    }

    .login-button {
      width: 100%;
      height: 100px;
      text-align: center;
      line-height: 100px;
      color: #fff;
      font-size: 32px;
      background: rgba(246, 53, 21, 0.5);
      @include borderRadius(60px);

      &.active {
        background: rgb(246, 53, 21);
      }
    }

    .quick-nav {
      padding: 40px 0;
      width: 100%;
      text-align: right;

      .register-button {
        color: #999;
        font-size: 28px;
      }
    }

    .other-login {
      width: 100%;
      margin-top: 100px;

      .other-head {
        @include fj;

        i {
          flex: 1;
          height: 1px;
          margin-top: 18px;
          background: #dcdcdc;
        }

        span {
          flex: 1;
          text-align: center;
          font-size: 28px;
          color: #dcdcdc;
        }
      }

      .other-con {
        display: flex;
        width: 320px;
        padding: 40px 0;
        margin: 0 auto;

        .login-icon {
          display: flex;
          flex-direction: column;
          width: 96px;
          margin: 0 30px;
          text-align: center;

          .iconfont {
            width: 100%;
            height: 96px;
            line-height: 96px;
            font-size: 60px;
            color: #15b8f5;
            background: #e7f7fe;
            @include borderRadius(50%);

            &.icon-weixin1 {
              font-size: 50px;
              color: #09bb07;
              background: #e6f8e6;
            }
          }

          span {
            padding-top: 20px;
            color: #999;
          }
        }
      }

      p {
        margin-top: 20px;
        width: 100%;
        text-align: center;
        color: #999;

        a {
          color: #409eff;
        }
      }
    }
  }
}
</style>