<template>
  <!-- 在模板字符串中使用 ${}  模板字符串是JS 中的-->
  <!-- Vue 使用 值綁定 v-bind: -->
  <van-swipe-cell :right-width="50" :beforeClose="beforeClose">
    <div class="box-container" @click="clickCellGood($event)">
      <!-- 复选框 -->
      <van-checkbox
        v-model="$store.getters.getCartList[currentIndex].checked"
        checked-color="#f00"
        @change="changeChecked"
      ></van-checkbox>
      <!-- 图片 -->
      <img :src="cart.imageHost + cart.mainImage" alt />
      <div class="content">
        <p class="title">{{ cart.name }}</p>
        <p class="subtitle">{{ cart.subtitle }}</p>
        <div class="shopcart-stepper">
          <span class="shopcart-price">￥ {{ cart.price}}</span>
          <van-stepper v-model="cart.count"  @plus="plus" @minus="minus" :disable-minus="isDisable" :disable-plus="isDisable"/>
          <!--  @change="changeValue" -->
        </div>
      </div>
    </div>
    <template slot="right">
      <van-button square type="danger" text="删除" />
    </template>
  </van-swipe-cell>
</template>

<script>
import { catchCartDel, catchartAdd } from "@/api";
import { Dialog } from "vant";

export default {
  props: {
    cart: {
      type: Object,
      // required 设置为毕传参数
      required: true
    },
    currentIndex: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      checked: this.$store.getters.getCartList[this.currentIndex].checked,
      // value: this.cart.count
      isDisable: false,
    };
  },
  created() {
    // console.log('购物车商品展示 组件： ' + this.cart.id);
  },
  methods: {
    //  步进器的 加和减 事件 响应 改变商品数量需要 发送给服务器
    plus() {
      // console.log('点击了加');
      let params = {
        productId: this.cart.productId,
        count: this.cart.count + 1
      }
      this.goodsNumber(params)
    },
    minus() {
      // console.log('点击了减');
      let params = {
        productId: this.cart.productId,
        count: this.cart.count - 1
      }
      this.goodsNumber(params)
    },

    goodsNumber(params) {
      // console.log(params);
      //1. 先删除 购物车对应的商品
      // 异步操作
        // 确定删除 执行 删除商品操作
        // 1. 向服务器发起请求， 删除 对应的购物车数据
        let delParams = { id: this.cart.id };
        this.isDisable = true, // 禁止点击

/*
正常开发 会提供 对应的 购物车商品 增加和减少的接口
*/ 
        catchCartDel(delParams).then(res => {
          if (res.status == "0") {
            // 2. 再追加商品到购物车
            // api
            catchartAdd(params).then(res => {
              // console.log(res);
              this.isDisable = false// 允许点击
            })
          }else {
            this.$toast('数据异常请刷新')
            this.isDisable = false// 允许点击
          }
        });
      
    },

    clickCellGood(e) {
      // console.log(e.target);
      // console.dir(e.target.className)
      // 过滤 步进器的事件 不在跳转详情页
      if (e.target.className == 'van-stepper__plus' || e.target.className == 'van-stepper__minus' || e.target.className == 'van-stepper__minus van-stepper__minus--disabled') {
        return false
      }

      // console.log('点击了商品：', this.cart);
      // 跳转到商品详情页
      this.$router.push('/product-detail/' + this.cart.productId)
    },
    // 删除 购物车商品
    // position 为关闭时点击的位置
    // instance 为对应的 SwipeCell 实例
    beforeClose({ position, instance }) {
      // console.log('....');
      switch (position) {
        case "cell":
        case "outside":
          instance.close();
          break;
        case "right":
          Dialog.confirm({
            message: "确定删除吗？",
            beforeClose: this.dialogClose // 异步处理事件
          })
            .then(() => {
              instance.close();
              // 确定删除 执行 删除商品操作
            })
            .catch(() => {
              // console.log("===取消删除 2===");
            });
          break;
      }
    },
    // 异步关闭 提示框
    dialogClose(action, done) {
      if (action == "confirm") {
        // 异步操作
        // 确定删除 执行 删除商品操作
        // 1. 向服务器发起请求， 删除 对应的购物车数据
        let params = { id: this.cart.id };
        catchCartDel(params).then(res => {
          if (res.status == "0") {
            // 请求删除 购物车商品成功
            // 2. 删除 本地 Vuex （store） 中对应得 购物车商品数据
            this.$store.commit("deleteGood", this.cart.id);
          }
          // 提示信息
          this.$toast(res.msg);
          done();
        });
      } else {
        // 关闭 弹窗
        // console.log("===取消删除 1===");
        done();
      }
    },

    // 步进器 值放生变化 触发的方法
    // changeValue(value) {
    //   // console.log('----', value);
    //   // cart 属于浅拷贝传递的 数据 所以可以 直接修改 到 store 中的state 中 cartList里面元素的 count值
    //   this.cart.count = value
    //   // 第二种方式 ：
    //   // this.$store.state.cartList[索引].count = value
    // },

    // 复选框 只发生变化触发的方法
    changeChecked() {
      this.checked = this.$store.getters.getCartList[this.currentIndex].checked;
      // console.log('复选框 只发生变化触发的方法', this.checked);
      // console.log(this.checked);
      // console.log(this.cart);
      // 修改 store 中 cartList 中对应的商品 选中状态
      this.cart.checked = this.checked;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "@/common/style/base.scss";

.box-container {
  display: flex;
  justify-content: space-between;
  // align-items: center;
  padding: 10px;
  margin: 0 0 20px;
  background-color: #fff;

  .van-checkbox {
    width: 60px;
    padding-right: 15px;
  }
  img {
    width: 160px;
    height: 160px;
    margin: 0 5px;
  }
  .content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-left: 10px;

    .title {
      height: 70px;
      font-size: 26px;
      overflow: hidden;
    }
    .subtitle {
      height: 70px;
      font-size: 24px;
      color: #ccc;
      overflow: hidden;
    }
    .shopcart-stepper {
      display: flex;
      justify-content: space-between;
      align-items: center;
      .shopcart-price {
        font-size: 30px;
        color: #f00;
      }
    }
  }
}
.van-button {
  height: 100%;
  width: 100px;
}
</style>