<template>
  <div>
    <!-- 顶部搜索栏 -->
    <header class="category-header">
      <!-- <van-icon name="wap-nav" color="#fff"/> -->
      <i class="iconfont icon-left" @click="goBack"></i>
      <div class="header-search">
        <i class="iconfont icon-search"></i>
        <span class="search-title">家电返场同价11.11</span>
      </div>
      <!-- 阿里妈妈的 字体图标 下面样式中设好了样式 -->
      <i class="iconfont icon-More"></i>

      <!--van 的字体图标 需要 在下面样式中设置样式 -->
      <!-- <van-icon name="ellipsis" />  -->
    </header>

    <!-- 分类 滚动区域 -->
    <div class="category-wrap">
      <!-- 左侧导航区域 -->
      <div class="nav-slide-wrapper" ref="slideWrap">
        <ul class="nav-slide">
          <!-- 
            touchend 事件 会存在 300 毫秒延迟 
            click 在 导入 fastclick 组件后 可以解决 300毫秒延时问题 也可以继续使用 click事件
          -->
          <li :class="{active: index === i}" v-for="(item, i) in categoryList" :key="i" @click="selectMenu(i)">{{ item.name }}</li>
        </ul>
      </div>

      <!-- 右侧内容区域 -->
      <div class="content-wrapper" ref="conWrap">
        <div class="content-slide" v-for="(item, i) in categorys" :key="i" >
          <img
            class="category-main-img"
            :src="item.mainImgUrl"
            alt
            v-if="item.mainImgUrl"
          />
          <div class="category-list" v-for="(categoryItem, caindex) in item.list" :key="caindex">
            <p class="catogory-title">{{ categoryItem.title }}</p>
            <div class="product-item" v-for="(productItem, proIndex) in categoryItem.productList" :key="proIndex">
              <img
                :src="productItem.imgUrl"
                class="product-img"
              />
              <p class="product-title">{{productItem.title}}</p>
            </div>
          </div>
         
        </div>
      </div>
    </div>

    <tab-bar />
  </div>
</template>

<script>
import tabBar from "@/components/tabBar.vue";
import BScroll from "@better-scroll/core";
import { catchCategoryList } from "@/api"

export default {
  components: {
    tabBar
  },
  data() {
    return {
      active: 1,
      categoryList: [],
      // 左侧快捷导航选中的 索引
      index: 0
    };
  },
  created() {    
    this.getcategoryList()
  },
  // 界面渲染完成之后
  mounted() {
    
  },
  // 当 data 数据 修改后 重新渲染界面完成 就会调用updated 钩子函数
  updated() {
    // this.$nextTick(() => {

    this.setWrapperHeight();
    // 设置 BScoll 需要控制的 节点对象
    let wrapper = document.querySelector(".nav-slide-wrapper");
    let scroll = new BScroll(wrapper);
    // 设置 BScroll 控制右侧内容的 滚动
    let rightWrap = document.querySelector('.content-wrapper')
    new BScroll(rightWrap)
    // console.log(scroll);
    // });
  },

  computed: {
    categorys: function () {
        // 筛选 左侧导航 选中的 数据 （左侧导航点击了 第几个 索引 就返回 这个 索引的数据）
        // console.log([this.categoryList[this.index]]);
        
        // 第一次 界面展示 this.categoryList 是没有 数据
        if(this.categoryList.length > 0) {
          return [this.categoryList[this.index]]
        } else {
          return []
        }
    }
  },

  methods: {
    // 返回上一级页面
    goBack() {
      this.$router.go(-1)
    },
    // 得到 左侧点击的 是第几个li标签
    selectMenu(i) {
      // console.log(i);
      this.index = i
    },
    // 获取 分类数据
    getcategoryList() {            
      catchCategoryList().then(res => {
        // console.log(res);
        this.categoryList = res;
      })
    },

    setWrapperHeight() {
      // 1.先 固定 wrap 的高度
      // 2. 获取设备屏幕高度
      const height = window.screen.height - 100;
      // 设置 wrapper 高度
      this.$refs.slideWrap.style.height = height + "px";
      // 设置 右侧 内容 高度
      this.$refs.conWrap.style.height = height + "px";
    }
  }
};
</script>

<style lang="scss" scoped>
@import "@/common/style/mixin.scss";

.category-header {
  position: fixed;
  left: 0;
  top: 0;
  @include fj;
  width: 100%;
  height: 100px;
  line-height: 100px;
  padding: 0 30px;
  @include boxSizing;
  font-size: 30px;
  color: #656771;
  z-index: 10000;
  background-color: #fff;
  &.active {
    background: $red;
  }

  .icon-left {
    font-size: 50px;
    font-weight: bold;
  }

  .header-search {
    display: flex;
    width: 80%;
    height: 40px;
    line-height: 40px;
    margin: 20px 0;
    padding: 10px 0;
    color: #232326;
    background: #f7f7f7;
    @include borderRadius(40px);

    .icon-search {
      padding: 0 20px 0 40px;
      font-size: 34px;
    }

    .search-title {
      font-size: 24px;
      color: #666;
    }
  }

  .icon-More {
    font-size: 40px;
  }
}
.category-wrap {
  @include fj;
  width: 100%;
  margin-top: 100px;
  background: #fff;
  border-top: 1px solid #dcdcdc;
  overflow: hidden;

  .nav-slide-wrapper {
    width: 30%;
    // height: 100%;
    // height: 800px;
    overflow: hidden;
    .nav-slide {
      width: 100%;
      @include boxSizing;
      background: #f8f8f8;
      li {
        width: 100%;
        height: 92px;
        text-align: center;
        line-height: 92px;
        font-size: 28px;

        &.active {
          color: $red;
          background: #fff;
        }
      }
    }
  }
  .content-wrapper {
    width: 70%;
    // height: 500px;
    // background-color: #f00;

    .content-slide {
      width: 100%;
      padding: 0 20px;
      background: #fff;
      @include boxSizing;

      .category-main-img {
        width: 100%;
      }

      .category-list {
        display: flex;
        flex-wrap: wrap;
        flex-shrink: 0;
        width: 100%;

        .catogory-title {
          width: 100%;
          font-size: 34px;
          font-weight: 500;
          padding: 40px 0;
        }

        .product-item {
          width: 33.3333%;
          margin-bottom: 20px;
          text-align: center;
          font-size: 30px;

          img {
            width: 100%;
          }
        }
      }
    }
  }
}
</style>