<template>
  <header>
    <i class="iconfont icon-left" @click="goBack"></i>
    <!-- <span>{{ title }}</span> -->
    <span>
      <!--
                 展示 插槽值
                 当组件渲染的时候，<slot></slot> 将会被替换为“Your Profile”。插槽内可以包含任何模板代码，包括 HTML
      -->
      <slot></slot>
    </span>
    <i class="iconfont icon-More" v-if="showMore"></i>
  </header>
</template>

<script>
export default {
  // props:['title'],
  // props:['showMore'],
  // 接收父组件传的值
  props: {
    showMore: {
      // type 指定 传递的值类型
      type: Boolean,
      default: false
    }
  },

  methods: {
    goBack() {
      this.$router.go(-1);
    }
  }
};
</script>

<style lang="scss" scoped>
@import "@/common/style/mixin.scss";

header {
  position: fixed;
  top: 0;
  z-index: 100;
  width: 100%;
  height: 88px;
  text-align: center;
  line-height: 88px;
  padding: 0 20px;
  font-size: 34px;
  @include boxSizing;
  background: #f7f7f7;

  // 使用弹性盒子布局
  // display: flex;
  // justify-content: space-between;

  .iconfont {
    position: absolute;
    left: 20px;
    top: 0;
    font-size: 50px;
    font-weight: bold;
  }
  .icon-More {
    left: auto;
    right: 20px;
  }
}
</style>