<template>
  <div class="recommend-list">
    <div class="recommend-item" v-for="item in commendList" :key="item.id" @click="clickCellGood(item.id)">
      <img :src="item.imageHost+item.mainImage" />
      <p>{{ item.name }}</p>
      <i>￥ {{ item.price }}</i>
    </div>
  </div>
</template>

<script>
import { catchreCommend } from "@/api";
export default {
  data() {
    return {
      commendList: []
    };
  },
  created() {
    this.getCommendList();
  },
  methods: {
    clickCellGood(productId) {
      // 跳转到商品详情页
      this.$router.push("/product-detail/" + productId);
    },
    // 获取推荐商品
    getCommendList() {
      catchreCommend().then(res => {
        // console.log(res);
        this.commendList = res;
      });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "@/common/style/mixin.scss";
.recommend-list {
  display: flex;
  flex-shrink: 0;
  flex-wrap: wrap;
  width: 100%;
  margin-top: 30px;
  padding-bottom: 150px;
  @include boxSizing;
  background: #fff;

  .recommend-item {
    display: flex;
    flex-direction: column;
    width: 50%;
    padding: 20px;
    @include boxSizing;
    border-bottom: 1px solid #dcdcdc;

    &:nth-child(2n-1) {
      border-right: 1px solid #dcdcdc;
    }

    img {
      width: 100%;
      height: 290px;
      margin: 0 auto;
    }

    p {
      height: 60px;
      padding: 20px 0;
      font-size: 26px;
      line-height: 40px;
      color: #333;
      overflow: hidden;
    }

    i {
      font-style: normal;
      font-size: 32px;
      color: $red;
    }
  }
}
</style>