import Vue from 'vue'
import VueRouter from 'vue-router'

// 引入首页、分类、购物车、用户信息
import Home from '../views/home/home.vue'
import Category from '@/views/category/category.vue'
import ShopCar from '@/views/shopCar/shopcar.vue'
import User from '@/views/user/user.vue'
import ProductList from '@/views/product-list/product-list.vue'
import ProductDetail from '@/views/product-detail/product-detail.vue'
import Login from '@/views/login/login'
import Profile from '@/views/profile/profile.vue'
/*
Git 源代码管理工具管理项目， 在 修改项目文件名称的 大小写后， 推送到服务端仓库，服务端的仓库对修改文件名称大小写的文件名没有记录
导致 再一次 clone 时， 还是使用的没有 修改名称之前的文件名

文件 名称修改到小写后 git 并没记录这个修改后的文件名称

简单的解决方案
将 文件 移出该项目目录 进行一次 git 提交记录
改好文件名后 再将文件添加进来 在提交一次 git 记录 就可以解决这一问题

*/ 

Vue.use(VueRouter)

// 配置 路由元信息， 判断页面 的层级关系
const routes = [
  {path: '/', redirect: '/home'},
  {path: '/home', component: Home, meta: {index: 1}},
  {path: '/category', component: Category, meta: {index: 1}},
  {path: '/shopcar', component: ShopCar, meta: {index: 1, auth: true}},
  {path: '/user', component: User, meta: {index: 1, auth: true}},
  {path: '/product-list/:categoryId', component: ProductList, meta: {index: 2}},
  {path: '/product-detail/:id', component: ProductDetail, meta: {index: 3}},
  {path: '/login', component: Login, meta: {index: 3}},
  {path: '/profile', component: Profile, meta: {index: 2, auth: true}}
]

const router = new VueRouter({
  routes
})

// 需要路由拦截 进行登陆判断
// 导航守卫 
/*
router.beforeEach((to, from, next) => {})
 将 在路由即将发生变化的时候 会触发这个方法

  to: 到哪里去
  from： 从哪里来

  next： 表示 执行完成后 必须调用的方法， 如果不调用 路由将不再向下继续执行，会取消掉当前 路由跳转
  next 如果未配置 参数 表示 继续执行 对应的跳转路径
  可以配置 跳转路径 作为 next('路径') 

*/ 
router.beforeEach((to, from, next) => {

  // 先判断路由元信息 是否 需要登录才能访问
  if (to.meta.auth) {
    if(localStorage.getItem('isLogin')) {
      next()
    } else {
      next('/login')
    }
  } else {
    next()
  }

  // // 如果 是 购物车 登陆 等页面 就需要进行登陆判断
  // if(to.path === '/user' || to.path === '/shopcar' || to.path === '/profile') {
  //   /*
  //   注意： 不要在 路由中 去访问 Vuex中 用户的登陆状态
  //    路由中 登陆 状态的判断 可以使用 localStorage 保存登陆状态
  //   */ 
  //   if(localStorage.getItem('isLogin')) {
  //     next()
  //   } else {
  //     // 如果是 未登录 状态 
  //     // 就跳转到 Login 页面
  //     next('/login')
  //   }
  // } else {
  //   // console.log('如果不写 next 方法 就会取消当前页面的跳转');
  //   next()
  // }
  
})



export default router
