<template>
  <div class="product-detail">
    <!-- <h3>商品详情 {{ this.$route.query.id}}</h3> -->
    <!-- <h3>商品详情 {{ this.$route.params.id}}</h3> -->
    <!-- 头部区域 -->
    <header class="detail-nav">
      <i class="iconfont icon-left" @click="backPage"></i>
      <div class="nav-list">
        <span :class="navIndex == 0 ? 'active' : ''" @click="selectItem(0)">
          <i class="iconfont icon-location" v-if="navIndex == 0"></i>商品
        </span>
        <span :class="navIndex == 1 ? 'active' : ''" @click="selectItem(1)">
          <i class="iconfont icon-location" v-if="navIndex == 1"></i>商品详情
        </span>
        <span :class="[{'active': navIndex == 2}]" @click="selectItem(2)">
          <i class="iconfont icon-location" v-show="navIndex == 2"></i>商品推荐
        </span>
      </div>
      <i class="iconfont icon-More"></i>
    </header>

    <!-- 轮播图 -->
    <section class="product-focus">
      <van-swipe :autoplay="3000" indicator-color="white" class="swiper-container">
        <!-- 展示 拼装 好的 轮播数据 -->
        <van-swipe-item v-for="(item, index) in sliderList" :key="index">
          <img :src="item" class="swiper_img" alt />
        </van-swipe-item>
      </van-swipe>
    </section>

    <!-- 详情信息 -->
    <section class="detail-info" ref="detailInfo">
      <p class="detail-info-name">{{ productDetail.name }}</p>
      <p class="detail-info-subtitle">{{ productDetail.subtitle }}</p>
      <div>
        <span class="detail-info-price">￥{{ productDetail.price }}</span>
        <span class="detail-info-stock">库存 {{ productDetail.stock }} 件</span>
      </div>
    </section>

    <!-- 商品详情 -->
    <section class="detail-content" ref="detailContent" id="content">
      <p class="detail-gap"></p>
      <ul>
        <li>概述</li>
        <li>参数</li>
        <li>安装服务</li>
        <li>常见问题</li>
      </ul>
      <!-- v-html="productDetail.detail" -->
      <div>
        <p>
          <img v-for="(img, index) in detailImgs" :key="index" alt="A973_01.png" :src="img" />
          <br />
        </p>
      </div>
    </section>

    <van-goods-action>
      <van-goods-action-icon icon="chat-o" text="客服" @click="onClickIcon" />
      <van-goods-action-icon icon="cart-o" text="购物车" @click="onClickIcon" />
      <van-goods-action-button type="warning" text="加入购物车" @click="onClickButton" />
      <van-goods-action-button type="danger" text="立即购买" @click="onClickButton" />
    </van-goods-action>

    <van-sku ref="skushow"
      v-model="show"
      :sku="sku"
      :goods="goods"
      :hide-stock="sku.hide_stock"
      @buy-clicked="onBuyClicked"
      @add-cart="onAddCartClicked"
    />
  </div>
</template>

<script>
import { catchProductDetail, catchartAdd } from "@/api";

export default {
  data() {
    return {
      // 记录路由地址中的 参数
      id: this.$route.params.id, //商品id
      // nav 上的 按钮编号记录
      navIndex: 0,
      productDetail: {}, // 详情数据
      sliderList: [], // 轮播数据
      detailImgs: [],
      //   配置 添加商品到 购物车 的信息展示
      show: false,
      sku: {
            tree: [{
                k: '颜色', // skuKeyName：规格类目名称
                v: [{
                        id: '001', // skuValueId：规格值 id
                        name: '红色', // skuValueName：规格值名称
                        imgUrl: 'http://img.happymmall.com/570186f3-c0d2-4336-a1b7-3e1aff88f73a.jpg', // 规格类目图片，只有第一个规格类目可以定义图片
                        previewImgUrl: 'http://img.happymmall.com/570186f3-c0d2-4336-a1b7-3e1aff88f73a.jpg', // 用于预览显示的规格类目图片
                    },
                    {
                        id: '002',
                        name: '蓝色',
                        imgUrl: 'http://img.happymmall.com/a73b6326-1204-4937-aa88-097c9eda8a2a.jpg',
                        previewImgUrl: 'http://img.happymmall.com/a73b6326-1204-4937-aa88-097c9eda8a2a.jpg',
                    }
                ],
                k_s: 's1' // skuKeyStr：sku 组合列表（下方 list）中当前类目对应的 key 值，value 值会是从属于当前类目的一个规格值 id
            }],
            list: [{
                id: 2259, // skuId，下单时后端需要
                price: 100, // 价格（单位分）
                s1: '001', // 规格类目 k_s 为 s1 的对应规格值 id
                stock_num: 50 // 当前 sku 组合对应的库存
            }, {
                id: 2259, // skuId，下单时后端需要
                price: 100, // 价格（单位分）
                s1: '002', // 规格类目 k_s 为 s1 的对应规格值 id
                stock_num: 50 // 当前 sku 组合对应的库存
            }],
            price: '100', // 默认价格（单位元）
            stock_num: 100, // 商品总库存
            collection_id: 2261, // 无规格商品 skuId 取 collection_id，否则取所选 sku 组合对应的 id
            none_sku: false, // 是否无规格商品
        },
      goods: {
        // 商品标题
        title: "测试商品",
        // 默认商品 sku 缩略图
        picture: "http://img.happymmall.com/a73b6326-1204-4937-aa88-097c9eda8a2a.jpg"
      },
    };
  },
  methods: {
    onClickIcon() {
      this.$toast("点击图标");
    },
    onClickButton() {
      this.$toast("点击按钮");
      this.show = true;
    },
    onBuyClicked() {
      // console.log("购买商品");
    },
    onAddCartClicked() {
      // console.log("添加到购物车");
      // 获取商品 添加到购物车商品的个数
    // console.log(this.$refs.skushow.selectedNum);
    
      // 1.调用添加购物车 请求
      // 2. 添加到 购物车的商品 更新到 store
      // 3. 跳转页面到 购物车页面
      let count = this.$refs.skushow.selectedNum
      let params = {
        productId: this.id,
        count
      }
      catchartAdd(params).then(res => {
          // console.log(res);
          if(res.status == '0') {
            // 
            // let cart = {
            //    "id": Math.random(),
            //   "productId": this.id,
            //   "count": count,
            //   "name": this.productDetail.name,
            //   "subtitle": this.productDetail.subtitle,
            //   "imageHost": this.productDetail.imageHost,
            //   "mainImage": this.productDetail.mainImage,
            //   "price": this.productDetail.price,
            // }
            // // 在异步请求中 更新 Vuex 的数据 缓存过的页面 会不被触发重新绘制页面
            // this.$store.commit('pushGoods', cart)
            this.$router.replace('/shopcar')
          }
          this.$toast(res.msg)
      })
    },

    backPage() {
      this.$router.go(-1);
    },
    /*
    点击 header 头部的 标签选项

    */
    selectItem(index) {
      this.navIndex = index;
      switch (index) {
        case 0:
          window.scroll(0, 0);
          break;
        case 1: //商品 详情选项
          /*
            ESlint 错误 Unexpected lexical declaration in case block (no-case-declarations)
            文档地址： https://cloud.tencent.com/developer/section/1135658
            解决办法 一、将 常量 声明 放置到 case 代码块 外面
            
            解决办法 二、将 eslint 配置文件 进行修改， 将 这一检测项配置关闭  "no-case-declarations": "off"

            修改 配置文件， 修改静态文件 需要重新 运行项目
            */
          const top = this.$refs.detailContent.offsetTop; // 详情 标签对象 距离整个页面顶部的 距离
          window.scroll(0, top - 44);
          break;
        case 2:
          window.scroll(0, 0);
          break;

        default:
          window.scroll(0, 0);
          break;
      }
    },

    getProductDetail() {
      const params = { id: this.id };
      catchProductDetail(params).then(res => {
        // console.log(res);
        this.productDetail = res;

        // 组装 轮播数据
        // console.log(this.productDetail.subImages);
        const list = this.productDetail.subImages.split(",");
        // console.log(list);

        // map() 方法创建一个新数组，其结果是该数组中的每个元素都调用一个提供的函数后返回的结果。
        /*
        拼装 轮播图片的完整地址
        
        */
        this.sliderList = list.map(item => {
          const imgUrl = this.productDetail.imageHost + item;
          // console.log(imgUrl);
          return imgUrl;
        });
        // console.log(this.silderList);
        // console.log(this.productDetail.detail);
        const re = /(\/\/\S+(\.jpg|\.png|\.jpeg|\.webp))/g;
        this.productDetail.detail.replace(re, substr => {
          // console.log(substr);
          this.detailImgs.push(substr);
        });
      });
    }
  },

  created() {
    this.getProductDetail();
  },
  //   生命周期方法 是 当DOM 元素渲染到浏览器中以后 调用的 生命周期钩子函数
  mounted() {
    // console.log(this.$refs.detailContent.offsetTop);
  }
};
</script>

<style lang="scss" scoped>
@import "@/common/style/mixin.scss";

.product-detail {
  width: 100%;

  .detail-nav {
    @include fj;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 88px;
    padding: 0 20px;
    line-height: 88px;
    box-sizing: border-box;
    z-index: 1000;
    color: #252525;
    background: #fff;
    border-bottom: 1px solid #dcdcdc;

    i {
      font-size: 50px;
      color: #000;
    }

    div span {
      padding: 0 20px;
      font-size: 28px;

      &.active {
        color: $red;
      }

      .iconfont {
        padding-right: 8px;
        font-size: 28px;
        color: $red;
      }
    }
  }
}
.product-focus {
  margin-top: 88px;

  .swiper-container {
    .swiper_img {
      width: 100%;
      height: 700px;
    }
  }
}
.detail-info {
  width: 100%;
  padding: 20px 30px;
  font-size: 30px;
  box-sizing: border-box;

  .detail-info-name {
    font-size: 40px;
    color: #333;
  }

  .detail-info-subtitle {
    padding: 10px 0;
    font-size: 28px;
    color: #999;
  }

  div {
    @include fj;
    height: 70px;
    line-height: 80px;
    padding: 10px 0;
    font-size: 32px;
    color: #999;

    .detail-info-price {
      color: $red;
      font-size: 44px;
    }
  }
}
.detail-content {
  width: 100%;

  .detail-gap {
    width: 100%;
    height: 20px;
    background: #eee;
  }

  ul {
    @include fj;
    width: 100%;
    margin: 20px 0;

    li {
      flex: 1;
      padding: 10px 0;
      text-align: center;
      font-size: 30px;
      border-right: 1px solid #999;
      box-sizing: border-box;

      &:last-child {
        border-right: none;
      }
    }
  }

  div {
    width: 100%;
    overflow: hidden;

    p {
      width: 100%;
      font-size: 40px;
      text-align: center;
      img {
        width: 100%;
        // height: 100%;
      }
    }

    img {
      width: 100%;
      height: 100%;
    }
  }
}
</style>