<template>
  <div id="app">

    <!-- 
        transition 设置 name 属性值 来控制过度动画 样式

        class="fade-rigth" 设置 class 是错误的
     -->
    <transition :name="transitionName">
      <router-view class="router-view"/>
    </transition>
  </div>
</template>

<script>
// fastclick 解决 手机端点击（触摸） 存在 300 毫秒延时问题
import fastclick from 'fastclick'
import { catchUserInfo, catchCartList} from '@/api'

export default {
  data() {
    return {
      // 记录过度动画的名称
      transitionName: 'fade-rigth'
    }
  },

  created() {
     if (!localStorage.getItem('zxgToken')) {
        // 如果 zxgToken 为 空就直接返回 不发送请求
        return null;
    }
    // 刷新页面时 可以 重新获取用户信息 将用户信息保存到 Vuex
     catchUserInfo().then(res => {
          // console.log(res);
          // 将 用户信息保存到 Vuex 中
          this.$store.commit("saveUserInfo", res);
     })
    //  this.getCartList()
    this.$store.dispatch('getCartList')
  },
  methods: {
// 获取购物车商品数据
    // getCartList() {
    //   catchCartList().then(res => {
    //     // console.log(res);
    //     // this.cartList = res;
    //     // 给每类 商品添加一个是否选中的属性
    //     let carts = res.filter(item => {
    //       item.checked = false;
    //       return item;
    //     });
    //     // console.log(carts);

    //     /**
    //      * $nextTick 等待 网路数据 请求完成后 再去更新 数据重现渲染 界面
    //      * */ 
    //     // DOM 还没有更新
    //     this.$nextTick(function() {
    //       // DOM 更新了
    //       this.$store.commit("saveShopcarts", carts);
    //     });
    //   });
    // }
  },

  mounted() {
    fastclick.attach(document.body);
  },

  // 监视器属性
  watch: {
    // 监听 路由变化
    $route: function (to, from) {
      // console.log(to);
      
      if(to.meta.index < from.meta.index) {
        // 做从左到右的动画
          this.transitionName = 'fade-left'
      } else if (to.meta.index > from.meta.index) {
        // 做从右到左的动画
        this.transitionName = 'fade-rigth'
      } else {

        // 层级 index 索引相等的 页面切换 不需要过度动画
        this.transitionName = ''
      }

    }
  }
}
</script>

<style lang="scss">


// .v-enter {
//   opacity: 0;
//   transform: translateX(100%)
// }
// .v-enter-active {
//   transition: all 0.6s ease;
// }

// 从右到左的 入场动画
.fade-rigth-enter,
.fade-left-leave-to { // fade-left-leave-to  从左到右的离场
  opacity: 0;
  transform: translateX(100%)
}

// 需要给谁加过度动画
.fade-rigth-enter-active,
.fade-left-enter-active,
.fade-left-leave-to,
.fade-rigth-leave-to{ // 一定要设置 入场 离场的 动画
  transition: all 0.6s ease;
}

// 从左边移动到右边
.fade-left-enter,
.fade-rigth-leave-to { // fade-rigth-leave-to 从右到左动画的离场动画
  opacity: 0;
  transform: translateX(-100%)
}


// 解决过度动画中间 留白的问题
.router-view {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  margin: 0 auto;
  // 滚动的 兼容样式
  -webkit-overflow-scrolling: touch;
}


</style>
