<template>
  <div>
    <!-- 顶部搜索栏 -->
    <!-- <header :class="['home-header', flage ? 'active' : '']"> -->
    <header :class="['home-header', {'active' : flage}]">
      <!-- <van-icon name="wap-nav" color="#fff"/> -->
      <i class="iconfont icon-caidan"></i>
      <div class="header-search">
        <span class="app-name">G</span>
        <i class="iconfont icon-search"></i>
        <span class="search-title">家电返场同价11.11</span>
      </div>
      <!-- <span>登录</span> -->
      <router-link tag="span" to="/login" v-if="!isLogin"> 登陆 </router-link>
      <router-link tag="i" to="/user" v-else class="iconfont icon-iconyonghu"> </router-link>

    </header>

    <!-- 轮播图 -->
    <van-swipe :autoplay="3000" indicator-color="white">
      <van-swipe-item v-for="(item, index) in sliders" :key="index">
        <img :src="item.imgUrl" alt />
      </van-swipe-item>
    </van-swipe>

    <!-- 分类 -->
    <section class="category-list">
      <div v-for="(item, index) in categoryList" :key="index" @click="goProductList(item)">
        <img :src="item.imgUrl" />
        <span>{{ item.name }}</span>
      </div>
    </section>

    <!-- 楼层 -->
    <section class="floor-list">
      <!-- floorList 第一次 循环 创建 楼层数 -->
      <div class="floor-wrap" v-for="(item, index) in floorList" :key="index">
        <img :src="item.headUrl" class="floor-head" />
        <div class="floor-content">
          <!-- 第二次循环 创建 商品数 -->
          <div class="floor-category" v-for="(pord, i) in item.list" :key="i">
            <p>{{ pord.title }}</p>
            <p>{{ pord.desc }}</p>
            <div class="floor-products">
              <!-- <img
                :src="pord.products[0].imgUrl"
              />
              <img
                :src="pord.products[1].imgUrl"
              />-->
              <!-- 根据商品 展示图的数据  创建 展示的图片标签 -->
              <img v-for="(img, imgIndex) in pord.products" :key="imgIndex" :src="img.imgUrl" alt />
            </div>
          </div>
        </div>
      </div>
    </section>

    <tab-bar />
  </div>
</template>

<script>
// 导入 自定义组件
import tabBar from "@/components/tabBar.vue";

// 每个需要请求的文件 都需要 引入 比较麻烦
// import axios from 'axios'

// 按需引入 API 模块
import { 
  catchSliders, 
  catchCategory, 
  catchFloorList, 
  catchUserInfo } from "@/api/index.js";

export default {
  components: {
    tabBar
  },
  data() {
    return {
      active: 0,
      // 节流阀属性
      flage: false,

      // 轮播图数据
      sliders: [],
      // 首页导航分类数据
      categoryList: [],
      // 首页楼层商品数据
      floorList: [],

      // 用戶信息
      // userInfo: {}
    };
  },

  computed: {
    /*
      判断用户是否为登录状态
    */ 
    isLogin() {
      // console.log('....123123');
      // console.log(this.$store.state.userInfo);
      // let objA = {};
      // let objB = {};
      // if (objA == objB) {
      //   console.log('111111');
      // } else {
      //   console.log('22222222');
      // }
      
      let isLo = this.$store.state.userInfo //=== undefined ? false : true
      
      return isLo
    }
  },
  methods: {
    
    // 获取用户信息
    // getUserInfo() {
    //   catchUserInfo().then(res => {
    //     console.log(res);
    //     this.userInfo = res
    //   })
    // },

    // 前往产品列表页面
    goProductList(item) {
      // console.log('前往产品列表');
      // console.log(item.categoryId);
      
      
      // 给出一个路由 地址
      // 可以使用 this.$router.push(路由地址) 前往路由地址
      // 可以使用 this.$router.go(-1) 返回上一级页面

      // 前往产品列表
      /*
      路由传参
      一、 params 方式:
      在 注册 路由的时候 需要 书写参数名
      {path: '/product-list/:categoryId', component: xxx}
      在 浏览器 地址栏 使用的时候 http://localhost:8080/product-list/100002
      

      二、 query 方式
      在 浏览器 地址栏 使用的时候 http://localhost:8080/product-list?categoryId=100002

      */ 
      this.$router.push('/product-list/' + item.categoryId)
    },

    // 监听页面滚动事件
    pageScroll() {
      // console.log('滚动了');
      // console.dir(window.pageYOffset);
      // 通过 判断 window 超出顶部的 距离，来设置 Header 的背景颜色
      if (window.pageYOffset > 100) {
        this.flage = true;
      } else {
        this.flage = false;
      }
    },

    // 获取 轮播数据
    getSliders() {
      /*
      引入 导出的 请求 对象
      catchSliders
    */

      //  因为 封装的 请求 是一个 promise 对象 （promise）
      // const res = catchSliders().then(res => {
      //   console.log(res);
      //   this.sliders = res;
      // });
      // console.log(res);

      catchSliders().then(res => {
        // console.log(res);
        this.sliders = res;
      });
    },
    // 获取首页导航分类
    getCategory() {
      catchCategory().then(res => {
        // console.log(res);
        this.categoryList = res;
      });
    },
    // 获取 首页楼层商品展示数据
    getFloorList() {
      catchFloorList().then(res => {
        this.floorList = res;
      });
    }
  },

  //
  created() {
    // 获取 轮播数据
    // console.log(this.$http);
    this.getSliders();
    this.getCategory();
    this.getFloorList();

    // this.getUserInfo()
  },
  // 生命周期方法， 标签元素渲染到 浏览器以后 会调用 该方法发
  mounted() {
    // 监听 window 的滚动事件
    window.addEventListener("scroll", this.pageScroll);
  }
};
</script>

<style lang="scss" scoped>
@import "@/common/style/mixin.scss";
.home-header {
  position: fixed;
  left: 0;
  top: 0;
  @include fj;
  width: 100%;
  height: 100px;
  line-height: 100px;
  padding: 0 30px;
  @include boxSizing;
  font-size: 30px;
  color: #fff;
  z-index: 10000;
  // background: $red;

  &.active {
    background-color: $red;
  }

  .icon-caidan {
    font-size: 50px;
  }

  .header-search {
    display: flex;
    width: 74%;
    height: 40px;
    line-height: 40px;
    margin: 20px 0;
    padding: 10px 0;
    color: #232326;
    background: #fff;
    @include borderRadius(40px);

    .app-name {
      padding: 0 20px;
      color: $red;
      font-size: 40px;
      font-weight: bold;
      border-right: 1px solid #666;
    }

    .icon-search {
      padding: 0 20px;
      font-size: 34px;
    }

    .search-title {
      font-size: 24px;
      color: #666;
    }
  }

  .icon-iconyonghu {
    font-size: 44px;
  }
}
.van-swipe {
  // height: 288px;
  background-color: skyblue;
  img {
    width: 100%;
  }
}

.category-list {
  display: flex;
  // 0 不允许 子盒子 收缩， 表示 子盒子设置的宽度不会被压缩
  flex-shrink: 0;
  flex-wrap: wrap;
  width: 100%;
  padding-bottom: 26px;

  div {
    display: flex;
    flex-direction: column;
    width: 20%;
    text-align: center;

    img {
      width: 80px;
      height: 80px;
      margin: 26px auto 16px auto;
    }
  }
}
.floor-list {
  width: 100%;
  padding-bottom: 100px;

  .floor-head {
    width: 100%;
    height: 80px;
    background: #f6f6f6;
  }

  .floor-content {
    display: flex;
    flex-shrink: 0;
    flex-wrap: wrap;
    width: 100%;
    @include boxSizing;

    .floor-category {
      width: 50%;
      padding: 20px;
      border-right: 1px solid #dcdcdc;
      border-bottom: 1px solid #dcdcdc;
      @include boxSizing;

      &:nth-child(2n) {
        border-right: none;
      }

      p {
        font-size: 34px;
        color: #333;

        &:nth-child(2) {
          padding: 10px 0;
          font-size: 26px;
          color: $red;
        }
      }

      .floor-products {
        display: flex;
        justify-content: space-around;
        width: 100%;

        img {
          width: 130px;
          height: 130px;
        }
      }
    }
  }
}
</style>