<template>
  <div>
    <!-- 头部区域 -->
    <div class="product-list-header">
      <!-- 头部搜索 -->
      <header class="category-header wrap">
        <i class="iconfont icon-left" @click="backHome"></i>
        <div class="header-search">
          <i class="iconfont icon-search"></i>
          <input type="text" class="search-title" v-model="value" />
        </div>
        <i class="iconfont icon-More"></i>
      </header>
      <!-- 排序 -->
      <section class="select-menu">
        <!-- 
              给元素绑定 自定义属性 orderby：default 、price-asc、price-desc 
              给元素绑定点击事件 onOrderByPrice
        -->
        <!-- 
            {'item-color': active == 'default'}
            因为 active 记录的 选中的选择按钮
            那么 如果 active 的值 等于 'default' 或者 。。。
            样式的属性值 为 true 就可以展示 这个 类名样式了
        -->
        <div
          :class="['select-item', {'item-color': active == 'default'}]"
          orderby="default"
          @click="onOrderByPrice"
        >默认排序</div>
        <div
          :class="['select-item', {'item-color': active == 'price_asc'}]"
          orderby="price_asc"
          @click="onOrderByPrice"
        >
          升序
          <i class="iconfont icon-up1"></i>
        </div>
        <div
          :class="['select-item', {'item-color': active == 'price_desc'}]"
          orderby="price_desc"
          @click="onOrderByPrice"
        >
          降序
          <i class="iconfont icon-down1"></i>
        </div>
        <div class="select-item">
          筛选
          <i class="iconfont icon-shaixuan"></i>
        </div>
      </section>
    </div>

    <section class="product-list">
      <div class="product-container">
        <!-- 循环商品数据 展示数据 -->
        <van-card
          v-for="(item) in list"
          :key="item.id"
          :num="item.stock"
          :price="item.price"
          :desc="item.subtitle"
          :title="item.name"
          :thumb="item.imageHost + item.mainImage"
          :origin-price="item.originalPrice"
          @click="goProductDetail(item)"
        />
      </div>
    </section>
  </div>
</template>

<script>
/*
文件查找 规则
引入文件夹中的文件， 如果 不写文件名
那么就会 先查找 有没有  index 文件
如果没有 在查找 index.二进制文件
如果没有 就查找 index.js
如果没有 就报错

如果 配置了 ESLint 需要 按 ESLint 的规则引入
可以在 ESLint 配置中关闭检测     'no-unused-vars': 'off'
*/

import { catchProductList } from "@/api";

export default {
  data() {
    return {
      // active 用来记录 点击的 筛选按钮
      active: "default", // 可以作为 请求参数的 orderBy
      categoryId: this.$route.params.categoryId,
      pageNum: 1,
      pageSize: 20,
      list: [],
      tempList: [], // 原始数据
      value: ""
    };
  },
  methods: {
    // 前往详情页面
    goProductDetail(item) {
      // console.log('点击了前往详情页面', item);

      // 跳转到商品详情页
      // this.$router.push('/product-detail?id=' + item.id) //query

      this.$router.push("/product-detail/" + item.id); // params 需要在 注册路由地址的 地方设置好 参数名
    },

    //   获取商品列表数据
    getPorductList() {
      const params = {
        categoryId: this.categoryId,
        orderBy: this.active,
        pageNum: this.pageNum,
        pageSize: this.pageSize
      };
      catchProductList(params).then(res => {
        // console.log(res);
        this.list = res.list;
        this.tempList = res.list;
      });
    },

    backHome() {
      // 返回首页
      this.$router.go(-1);
    },
    /*
    监听 排序按钮点击事件 获取点击的事件对象
    从事件对象中 获取 被点击的 标签
    */

    onOrderByPrice(event) {
      // console.log(event.target);
      // 获取响应 事件的 标签对象
      // 通过标签的 自定义 属性 区分标签 对象
      const orderby = event.target.getAttribute("orderby");
      // console.log(orderby);
      //   改变 active 的值, 因为 active 是用来记录 被选中 的 筛选按钮的
      this.active = orderby;
      // console.log(this.active);

      // 重新 排序好的数据 请求回来
      //  const params = {
      //   categoryId: this.categoryId,
      //   orderBy: this.active,
      //   pageNum: this.pageNum,
      //   pageSize: this.pageSize
      // };
      //  catchProductList(params).then(res => {
      //   console.log(res);
      //   this.list = res.list
      // });
      this.getPorductList();
    }
  },
  created() {
    // console.log(this.$route.params.categoryId);
    //   获取商品列表数据
    this.getPorductList();
  },
  watch: {
    // 本地数据 模糊搜索
    // 监听 value 值的变化
    value: function (newValue) {
      // console.log(newValue, this);
      // 1.触发监听方法 就遍历 tempList原始数据 匹配list 元素中的属性值 是否包含 value
      let newArr = this.tempList.filter(item => {
        // 匹配 商品 name 是否包含输入放入 字符串
        if (item.name.indexOf(newValue) != -1) {
          return item;
        }
        // 匹配 商品 subtitle 是否包含输入放入 字符串
        if (item.subtitle.indexOf(newValue) != -1) {
          return item;
        }
        // 匹配 商品 price 是否包含输入放入 字符串
        if (item.price == newValue) {
          return item;
        }
      });

      this.list = newArr
    }

    
    // 2.如果 包含 那么久 加载空数组中，
    // 3. 遍历完数据 就设置给 list
    // 4. 当监听 value 值为 空 那么就将 tempList原始数据 设置给 list
  }
};
</script>

<style lang="scss" scoped>
@import "@/common/style/mixin.scss";

.product-list-header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 1000;
  background: #fff;

  .category-header {
    @include fj;
    width: 100%;
    height: 100px;
    line-height: 100px;
    padding: 0 30px;
    @include boxSizing;
    font-size: 30px;
    color: #656771;
    z-index: 10000;

    &.active {
      background: $red;
    }

    .icon-left {
      font-size: 50px;
      font-weight: bold;
    }

    .header-search {
      display: flex;
      width: 76%;
      height: 40px;
      line-height: 40px;
      margin: 20px 0;
      padding: 10px 0;
      color: #232326;
      background: #f7f7f7;
      @include borderRadius(40px);

      .icon-search {
        padding: 0 10px 0 40px;
        font-size: 34px;
      }

      .search-title {
        font-size: 24px;
        color: #666;
        background: #f7f7f7;
      }
    }

    .icon-More {
      font-size: 40px;
    }

    .search-btn {
      height: 70px;
      margin: 15px 0;
      line-height: 70px;
      padding: 0 10px;
      color: #fff;
      background: $red;
      @include borderRadius(10px);
    }
  }

  .select-menu {
    display: flex;
    justify-content: space-around;
    width: 100%;
    height: 100px;
    background: #fff;

    .select-item {
      flex: 1;
      height: 100%;
      text-align: center;
      line-height: 100px;
      font-size: 30px;
      border-top: 1px solid #dcdcdc;
      border-bottom: 1px solid #dcdcdc;

      &.item-color {
        color: $red;
      }
      .iconfont {
        &.icon-down1 {
          color: #999;
          padding-left: 10px;
          font-size: 22px;
        }

        &.icon-up1 {
          @extend .icon-down1;
          font-size: 34px;
        }

        &.icon-shaixuan {
          @extend .icon-down1;
          font-size: 28px;
        }

        &.active {
          color: $red;
        }
      }
    }
  }
}
.product-list {
  width: 100%;
  .product-container {
    position: absolute;
    left: 0;
    top: 220px;
    width: 100%;
    overflow-y: scroll;
    height: auto;
  }
}
</style>