import Vue from 'vue'
import Vuex from 'vuex'
import { catchCartList } from '@/api'
Vue.use(Vuex)

export default new Vuex.Store({
  // Vuex state 中的数据 会在 浏览器刷新的时候 清空里面的数据
  state: {
    userInfo: null,
    cartList: []
  },
  mutations: {
    // 保存用户信息
    saveUserInfo(state, uInfo) {
      state.userInfo = uInfo
    } ,
    // 清除用户信息
    removeUserInfo(state) {
      state.userInfo = null
      // 清除 购物车 本地数据
      state.cartList = []
    },

    // 保存 购物车商品数据
    saveShopcarts(state, carts) {
      state.cartList = carts
    },

    // 修改商品 是否选中的状态
    updateGoods(state, checked) {
      // console.log('-updateGoods------', checked);
      // if (checked == false) {
      //   return
      // }
      // 遍历 商品 改变商品的 选中状态
      state.cartList.forEach(item => {
        item.checked = checked
      })
    },
    // 改变 某一个商品的选中状态
    // updateGoodItem(state, checked) {
    // }

    // 上购物车 商品
    deleteGood(state, id) {
      // 通过商品的 购物车 id 找到对应的商品数据 删除 对应的 商品 数据
      state.cartList.forEach((item, index) => {
        if (item.id === id) {
          state.cartList.splice(index, 1)
        }
      })
    },

    // 添加商品到购物车
    pushGoods(state, cart) {
      // 点判断 本地是都 保存有该商品
      let isPush = true
      let arr = state.cartList.filter(item => {
        // console.log('遍历商品 id 对比： ' ,item.productId, cart.productId);
        if (item.productId == cart.productId) { // 对比 商品 id
          item.count += cart.count
          isPush = false
        }
        return item
      })
      // 本地没有保存该商品
      if (isPush) {
        arr.push(cart)
      }
      // 将添加好的商品 数据 覆盖 cartList
      state.cartList = arr
    }

  },
  getters: {
    // 返回 商品数据 
    getCartList(state) {
      return state.cartList
    },
    // 计算商品的件数
    getCounts(state) {
      // 遍历商品 计算 商品的总件数
      let allCounts = 0
      state.cartList.forEach(item => {
        // console.log(item);
        allCounts += item.count
      })
      return allCounts
    },
    getPrices(state) {
      let allPrice = 0
      state.cartList.forEach(item => {
        // 是选中的商品 就累加总价格
        if(item.checked) {
          allPrice += item.price * item.count
        }
      })
      // 返回计算好的总价格
      return allPrice
    },

    // 当某一 商品 选中状态 发生变化时 改变 全选的选中状态
    getAllChecked(state) {
        let isChecked = true
        state.cartList.forEach(item => {
          if(!item.checked) {
            // 只要有一件商品 为  非选中 那么 全选就不选中
            isChecked = false
          }
        })

        // if (isChecked) { // 如果 isChecked 为真 那么就需要判断 购物车列表是否有值， 如果没有值就返回false
        //   if (state.cartList.length > 0) {
        //     return isChecked
        //   } else {
        //     return false
        //   }
        // } else {
        //   return isChecked
        // }
          // 等同下面的 三元运算
        return isChecked ? (state.cartList.length > 0 ? isChecked : false) : isChecked
    }

  },
  actions: {
    /** actions：
     * 1.可以 异步触发 mutations 中的方法
     * 2. actions 中的方法 第一个参数 为 context， context 与 store 实例具有一样的属性和方法
     * */ 
    // 获取购物车商品数据
    getCartList(context) {
      catchCartList().then(res => {
        // 给每类 商品添加一个是否选中的属性
        let carts = res.filter(item => {
          item.checked = false;
          return item;
        });
        /*
        因此你可以调用 context.commit 提交一个 mutation，或者通过 context.state 和 context.getters 来获取 state 和 getters。
        */ 
        context.commit('saveShopcarts', carts)
        /**
         * $nextTick 等待 网路数据 请求完成后 再去更新 数据重现渲染 界面
         * */ 
        // // DOM 还没有更新
        // this.$nextTick(function() {
        // DOM 更新了
        // this.$store.commit("saveShopcarts", carts);
        // });
      });
}




  },
  modules: {

  }
})
