/*
NodeJS 中 导出 模块
导出模块 module.exports = {} 或 exports.属性名 = 值

这是 NodeJS 导出数据的方式

*/ 

/*
Vue 中导出 组件的 方式

第一种方式 完整导出 和 导入
    完整导出 export default { ... }
    完整导入 import 组件名或数据名称 from '来自哪一个路径'
             import xxx from './'

第二种方式 按需导出 和 导入
    按需导出：    export const a = 10;
                  export const b = 30;
                  export const b = 35;

    按需导入：
    import {a, b} from './api/index.js'
    console.log(a, b);

    按需导出的 名字 与 按需导入的名字需要 一一对应
*/ 

// 封装 请求
import axios from 'axios'
// 需要 引入组件 Toast 
import { Toast } from 'vant'
// 引入 qs 组件
import qs from 'qs'
// 导入 router 实例
import router from '../router'

// 全局的 axios 默认值
axios.defaults.baseURL = 'http://www.lovegf.cn:9527/api';

/*
全局 请求拦截器
第一个方法 是全局拦截 请求 数据
第二个方法 是全局拦截 响应 数据 
*/ 
// 全局拦截器 可以判断 接口获取返回的数据 是否正确
// 添加响应拦截器
axios.interceptors.response.use(function (response) {
    
    // 对响应数据做点什么
    if(response.data.status == -2) {
        /*
            状态码 -2 表示 token 失效
            清除当前 保存的 token 数据
            清除当前 登陆记录
            让页面 回到首页
        */ 
       localStorage.removeItem('zxgToken')
       localStorage.removeItem('isLogin')
        // this.$router.replace('/home')
        router.replace('/home')
    }


    return response;
  }, function (error) {
    // 对响应错误做点什么
    return Promise.reject(error);
  });


/* --------------------------------------------首页-------------------------------------*/ 
// 获取 轮播数据
export const catchSliders =  () => {
    return axios.get("/home/sliders").then(res => {
                // 判断数据 是否请求成功
                if (res.data.status == 0) {
                    return res.data.data;
                } else {
                    // console.log("获取 轮播数据失败");
                    Toast("接口错误");
                }
            }).catch(err => {
                // console.log(err);
                Toast("接口错误");
            });
}


// 获取首页导航分类
export const catchCategory = () => {
    return  axios.get('/home/category').then(res => {
        if (res.data.status == 0) {
            return res.data.data
        } else {
            Toast('接口异常')
        }
    }).catch(err => {
        Toast('接口异常' + err.message)
    })
}


