// 获取 商品列表数据
export const catchProductList = (params) => {

    // 参数的设置方式 这样写存在问题
    // return axios.get('/product/list', params).then(res => {
    //                     if(res.data.status == 0) {
    //                         return res.data.data
    //                     } else {
    //                         // 使用 接口返回的提示信息 
    //                         Toast(res.data.msg)
    //                     }
    //                 }).catch(err => {
    //                     Toast('接口异常'+ err.message)
    //                 })

    // 发送 带参数  以 query 方式提交 可以 这样传参
    return axios.get('/product/list', {
        'params': params
    }).then(res => {
            if(res.data.status == 0) {
                return res.data.data
            } else {
                // 使用 接口返回的提示信息 
                Toast(res.data.msg)
            }
        }).catch(err => {
            Toast('接口异常'+ err.message)
        })
}

