// 登陆
export const catchUserLogin = params => {   

    // '?username=zxx&password=123456' post 表单格式
    // 使用 qs 组件 将对象转换成 表单数据格式
    // console.log(qs.stringify(params));
    /*
        如果 后台 支持接收 Json 字符串的参数 可以直接 传参
        如果 后台 只支持接收 表单格式的数据 就需要 将参数转换成表单格式
    */ 
    
    return axios.post('/user/login', qs.stringify(params)).then(res => {
        // 将请求数据 返回给页面， 页面自己做处理
            return res.data
                // if(res.data.status == 0) {
                //     return res.data.data
                // } else {
                //     // 使用 接口返回的提示信息 
                //     Toast(res.data.msg)
                // }
            }).catch(err => {
                Toast('接口异常'+ err.message)
            })
}

// 获取用户 信息
export const catchUserInfo = () => {    
    // console.log('排查请求问题：');
    // console.log(localStorage.getItem('zxgToken'));
    return axios.get('/user/info', {
        // headers 设置请求头
        headers: {
            // 将需要配置 的请求头 key 和值设置进来
            // 因为登陆后保存了 token 所以 可以直接 获取保存好的 token
            Authorization: localStorage.getItem('zxgToken')
        }
    }).then(res => {
                // console.log(res.data);
                if(res.data.status == 0) {
                    return res.data.data
                } else {
                    // 使用 接口返回的提示信息 
                    /*
                        当 token 过期 需要清除用户信息
                    */ 
                    Toast(res.data.msg)
                }
            }).catch(err => {
                Toast('接口异常'+ err.message)
            })
}
// 退出登陆
export const catchUserLogout = params => {       
    /*
    注意： get 的 Header 设置 和 post 的 Header 设置 位置不一样

        axios.post: 请求 
            第一个参数 请求地址
            第二个参数 请求参数（如果请求参数为空设置 null）
            第三个参数 设置请求头
    */ 
    return axios.post('/user/logout', null, {
        headers: {
            Authorization: localStorage.getItem('zxgToken')
        }
    }).then(res => {
        // 将退出登陆 后的信息直接返回
        return res.data

                // if(res.data.status == 0) {
                //     return res.data.data
                // } else {
                //     // 使用 接口返回的提示信息 
                //     Toast(res.data.msg)
                // }
    }).catch(err => {
        Toast('接口异常'+ err.message)
    })
}

